<template>
    <div class="main-content">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon">
                    </i>
                    <a href="#">
                    </a>
                </li>
                <li class="active">
                    <router-link to="/whd">
                        微互动
                    </router-link>
                </li>
            </ul>
        </div>
        <div class="page-content ha_page_content">
            <div class="el-row contBox clear">
                <div class="cs-filter-box">
                    <!-- 筛选 -->
                    <div class="cs-btn">
                        <el-button :plain="plain" @click="shuidata" type="primary">
                            用水数据
                        </el-button>
                        <el-button :plain="!plain"  @click="diandata" type="primary">
                            用电数据
                        </el-button>
                    </div>
                </div>
                <div class="cs-filter-box wsj-filter-box">
                    <el-col :span="6">
                        <span class="cs-sj">
                            起止时间
                        </span>
                        <el-date-picker placeholder="选择日期范围" type="daterange" v-model="time">
                        </el-date-picker>
                    </el-col>
                    <el-col :span="10">
                        <el-select placeholder="请选择" v-model="value">
                            <el-option :label="item.label" :value="item.value" v-for="item in options">
                            </el-option>
                        </el-select>
                        <el-select placeholder="请选择" v-model="value">
                            <el-option :label="item.label" :value="item.value" v-for="item in options">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="8">
                        <el-col :span="8">
                            <el-button>
                                数据导入
                            </el-button>
                        </el-col>
                        <el-col :span="16">
                            <el-input :on-icon-click="handleIconClick" icon="search">
                            </el-input>
                        </el-col>
                    </el-col>
                </div>
                <div class="cs-list-table">
                    <!-- 列表 -->
                    <el-table :data="tableData" border="" style="width: 100%;">
                        <el-table-column label="序号" prop="" width="100">
                        </el-table-column>
                        <el-table-column label="年份" prop="">
                        </el-table-column>
                        <el-table-column label="月份" prop="">
                        </el-table-column>
                        <el-table-column label="户号" prop="">
                        </el-table-column>
                        <el-table-column label="户名" prop="">
                        </el-table-column>
                        <el-table-column label="联系电话" prop="">
                        </el-table-column>
                        <el-table-column label="地址" prop="">
                        </el-table-column>
                        <el-table-column :label="type" prop="">
                        </el-table-column>
                    </el-table>
                    <el-pagination :total="50" layout="prev, pager, next">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
	methods: {
	  handleClick() {
		console.log(1);
	  },
	  shuidata(){
		this.plain=false;
		this.type="用水量（立方米）";
	  },
	  diandata(){
	  	this.plain=true;
	  	this.type="用电量（千瓦时）"
	  }
	},

	data() {
	  return {
	  	plain:false,
		options:"",
		value:"",
		time:"",
		tableData: [],
		type:"用水量（立方米）",
	  }
	}
  }
</script>
<style scoped="">
    .wsj-filter-box  .wsj-searchBox{
	  margin-top: 0;
  }
  .cs-sj{
	padding-right:25px;
  }
  .cs-btn{
	padding-top: 20px;
	height: 40px;
  }
  .el-input__inner,.el-input__icon{
	padding-right: 0;
  }
</style>


